.App {
  margin: auto;
  max-width: 800px;
  width: calc(100% - 2rem);
}

.con {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.con .box {
  padding: 2rem;
  border-radius: 2rem;
  border: 5px solid lightgray;
  text-align: center;
}

.con .box img {
  width: 80%;
  height: 80%;
  object-fit: cover;
}

.btnCon {
  width: calc(50% - 1rem);
  text-align: center;
  padding: 2rem;
}

.win {
  background-color: lightpink;
  border-color: tomato;
}

.lose {
  background-color: greenyellow;
  border-color: yellowgreen;
}
